import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import WazirxCloneScreen from "../screens/wazirx";
import ContactUs from "../components/common/move-forward";
import Faq from "../components/common/faq";

const WazirxCloneScript = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "WazirX Clone Script", link: null },
  ];
  const wazirxFaqData = [
    {
      question: "Is the WazirX clone script customizable?",
      answer: "Yes, WazirX clone scripts are highly customizable.",
    },
    {
      question: "Can I launch my cryptocurrency exchange like WazirX?",
      answer:
        "Yes, our WazirX clone script enables you to quickly launch your WazirX-like exchange platform.",
    },
    {
      question:
        "How long does launching a crypto exchange using your WazirX clone script take?",
      answer:
        "Launch time typically ranges from weeks to a few months based on your requirements.",
    },
    {
      question:
        "Which cryptocurrencies are supported by your WazirX clone script?",
      answer:
        "The script supports various cryptocurrencies, including Bitcoin, Ethereum, and more.",
    },
    // {
    //   "question": "How secure is the WazirX clone script?",
    //   "answer": "Our WazirX clone script prioritizes security, ensuring robust protection for users and their assets."
    // },
    // {
    //   "question": "Does your WazirX clone script support mobile devices?",
    //   "answer": "Yes, it's typically designed to be mobile-friendly."
    // },
    // {
    //   "question": "What advantages does your WazirX clone script offer?",
    //   "answer": "Our clone script provides a fast and cost-effective solution with all essential features."
    // },
    // {
    //   "question": "Do I need technical expertise to use the clone script?",
    //   "answer": "No, our clone script is designed to be user-friendly, requiring only basic technical knowledge to operate."
    // }
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <WazirxCloneScreen db={db} />;
      <Faq faqData={wazirxFaqData} />
      <ContactUs db={db} />
    </Layout>
  );
};

export const Head = () => {
  return (
    <Seo
      title="Wazirx Clone Script | Crypto Exchange Script "
      headTitle="Wazirx Clone Script | Crypto Exchange Script "
      description="WazirX clone script is a powerful solution to launch your crypto exchange. It offers essential features for a quick start, mirroring the WazirX exchange."
      keywords=" Wazirx Clone Script, crypto exchange like WazirX, crypto exchange platform,crypto exchange clone scripts,WazirX Clone App Development,WazirX clone app,WazirX Clone App Development Process,WazirX clone software,WazirX-Like Crypto Exchange,WazirX-like crypto exchange development,WazirX exchange, crypto exchange development company
      "
    />
  );
};

export default WazirxCloneScript;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
